@charset "UTF-8";
[v-cloak] {
  display: none !important; }
/* 清除默认样式 */
body, h1, h2, h3, h4, h5, h6, p, ol, ul, dl, dt, dd, input, textarea {
  padding: 0;
  margin: 0; }
body {
  padding-top: constant(safe-area-inset-top);
