h1, h2, h3, h4, h5, h6 {
  font-weight: normal; }
li {
  list-style: none; }
a {
  text-decoration: none;
  color: #666; }
img {
  vertical-align: top;
  border: none; }
div, input, textarea {
  border: none;
  outline: none;
  background: none;
  -webkit-appearance: none;
  -webkit-tap-highlight-color: transparent; }
textarea {
  resize: none; }
body {
  width: 100%;
  height: 100%;
  font-size: 16px; }
.w {
  width: 1000px;
  margin: 0 auto; }
.clearfix:after {
  content: '';
  display: block;
  clear: both; }
